import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { message } from 'antd';
import RuleForm from '../compoents/RuleForm';

function updateSetting(values: any) {
  axios.post('api/updateSetting', values).then(() => {
    message.success('设置成功');
  });
}

const Setting: React.FC = () => {
  const [rule, setRule] = useState({
    prefix: '',
    content: '',
    suffix: '',
  });
  useEffect(() => {
    axios.get('/api/getSetting').then((res) => {
      setRule(res.data);
    });
  }, []);
  return (
    <div style={{ textAlign: 'center' }}>
      <div
        style={{
          width: '680px',
          margin: '0 auto',
          backgroundColor: '#fff',
          padding: 20,
        }}
      >
        <RuleForm {...rule} onConfirm={updateSetting}></RuleForm>
      </div>
    </div>
  );
};

export default Setting;
