import React, { useState, useEffect } from 'react';
import { Card, Row, Col, Empty, Input } from 'antd';
import { useHistory } from 'react-router-dom';
import axios from 'axios';
import { Package } from './types';

const List: React.FC = () => {
  const [list, setList] = useState<Package[]>([]);
  const history = useHistory();
  useEffect(() => {
    axios.get('/api/list').then((res) => {
      setList(res.data);
    });
  }, []);
  // 筛选
  const [query, setQuery] = useState('');

  return (
    <div>
      <div>
        <Input
          size='large'
          style={{ width: '300px', marginLeft: 10 }}
          value={query}
          onChange={({ target }) => setQuery(target.value)}
          placeholder='请输入模版名搜索'
        ></Input>
      </div>
      <Row>
        {list && list.length > 0 ? (
          list
            .filter((item) => item.name.indexOf(query) !== -1)
            .map((item) => (
              <Col
                key={item.name}
                xs={24}
                sm={24}
                md={12}
                lg={6}
                style={{ padding: 10 }}
              >
                <Card
                  title={item.name}
                  extra={item.version}
                  onClick={() => history.push(`/creator/${item.name}/${item.version}`)}
                >
                  <pre>{item.description || '暂无简介'}</pre>
                  <p style={{ textAlign: 'right' }}>作者：{item.author}</p>
                </Card>
              </Col>
            ))
        ) : (
          <Empty></Empty>
        )}
      </Row>
    </div>
  );
};

export default List;
