import React, { useEffect, useState } from 'react';
import { Form, Input, Button } from 'antd';

interface RuleForm extends Rule {
  onConfirm(values: any): void;
}

const Setting: React.FC<RuleForm> = (props) => {
  const { onConfirm, prefix, content, suffix } = props;
  const [rule, setRule] = useState({
    prefix: '',
    content: '',
    suffix: '',
  });
  const [form] = Form.useForm();
  useEffect(() => {
    setRule({
      prefix,
      content,
      suffix,
    });
    form.setFieldsValue({
      prefix,
      content,
      suffix,
    });
  }, [prefix, content, suffix]);
  return (
    <div style={{ textAlign: 'center' }}>
      <div
        style={{
          width: '100%',
          margin: '0 auto',
          backgroundColor: '#fff',
          padding: 20,
        }}
      >
        <h2>规则设置</h2>
        <Form
          form={form}
          onFinish={onConfirm}
          labelCol={{ span: 6 }}
          onValuesChange={(cv, av: any) => setRule(av)}
        >
          <Form.Item name="prefix" label="前缀" required>
            <Input
              prefix={
                <span style={{ color: '#1890ff', fontWeight: 'bold' }}>
                  {'/(?<='}
                </span>
              }
              suffix={
                <span style={{ color: '#1890ff', fontWeight: 'bold' }}>)</span>
              }
              placeholder="前匹配"
            ></Input>
          </Form.Item>
          <Form.Item name="content" label="匹配" required>
            <Input
              prefix={
                <span style={{ color: '#1890ff', fontWeight: 'bold' }}>(</span>
              }
              suffix={
                <span style={{ color: '#1890ff', fontWeight: 'bold' }}>
                  *?)
                </span>
              }
              placeholder="请输入正确的正则表达式"
            ></Input>
          </Form.Item>
          <Form.Item name="suffix" label="后缀" required>
            <Input
              prefix={
                <span style={{ color: '#1890ff', fontWeight: 'bold' }}>
                  (?=
                </span>
              }
              suffix={
                <span style={{ color: '#1890ff', fontWeight: 'bold' }}>
                  )/gm
                </span>
              }
              placeholder="后匹配"
            ></Input>
          </Form.Item>
          <Form.Item label="预览">
            <div style={{ color: 'red', textAlign: 'left' }}>
              {`/(?<=${rule.prefix})(${rule.content}*?)(?=${rule.suffix})/gm`}
            </div>
          </Form.Item>
          <Form.Item>
            <Button type="primary" htmlType="submit">
              保存
            </Button>
          </Form.Item>
        </Form>
      </div>
    </div>
  );
};

export default Setting;
