import React from 'react';
import { Form, Input, Button, Upload, message } from 'antd';
import { UploadProps } from 'antd/es/upload';
import {
  UploadOutlined,
  PlusOutlined,
  MinusCircleOutlined,
} from '@ant-design/icons';
import axios from 'axios';

interface MyUploadProps extends UploadProps {
  value?: string;
  onChange?(info: any): void;
}
const MyUpload: React.FC<MyUploadProps> = (props) => {
  const { value, onChange, ...others } = props;
  const handleChange = (info: any) => {
    if (info.file.status !== 'uploading') {
      console.log(info.file, info.fileList);
    }
    if (info.file.status === 'done') {
      onChange &&
        onChange({ name: info.file.name, url: info.file.response.data.url });
      message.success(`${info.file.name} file uploaded successfully`);
    } else if (info.file.status === 'error') {
      message.error(`${info.file.name} file upload failed.`);
    }
  };
  const handleRemove = () => {
    onChange && onChange('');
  };
  return (
    <Upload {...others} onChange={handleChange} onRemove={handleRemove}>
      {!value && (
        <Button>
          <UploadOutlined /> 点击上传
        </Button>
      )}
    </Upload>
  );
};

function savePackage(values: any) {
  console.log(values);
  axios.post('/api/savePackage', values).then((res) => {
    if (!res.data.success) {
      message.error(res.data.message);
    } else {
      message.success('操作成功');
    }
  });
}

interface TemplateFile {
  file: any;
  description: string;
  readme: any;
}

interface Package {
  name: string;
  author: string;
  version: string;
  files: TemplateFile[];
}

const TemplateUpload: React.FC = () => {
  const [form] = Form.useForm();

  const uploadConfig = {
    name: 'file',
    action: '/api/upload',
  };
  return (
    <div>
      <div
        style={{
          width: '680px',
          margin: '0 auto',
          backgroundColor: '#fff',
          padding: 20,
        }}
      >
        <Form form={form} labelCol={{ span: 4 }} onFinish={savePackage}>
          <Form.Item name='name' label='模版名' rules={[{ required: true, message: '请输入模版名！' }]}>
            <Input></Input>
          </Form.Item>
          <Form.Item name='description' label='概要'>
            <Input.TextArea autoSize={{ minRows: 2 }}></Input.TextArea>
          </Form.Item>
          <Form.Item name='version' label='版本号' rules={[{ required: true, message: '请输入版本号！' }]}>
            <Input></Input>
          </Form.Item>
          <Form.Item name='author' label='作者' rules={[{ required: true, message: '请输入作者！' }]}>
            <Input></Input>
          </Form.Item>
          <Form.List name='files'>
            {(fields, { add, remove }) => {
              return (
                <div>
                  {fields.map((field, index) => (
                    <div key={field.key} className='template-ctn'>
                      <p className='template-title'>模版文件{index + 1}</p>
                      <Form.Item
                        name={[field.name, 'template']}
                        // fieldKey={[field.fieldKey, 'template']}
                        label='模版文件'
                        rules={[{ required: true, message: '请上传模版！' }]}
                      >
                        <MyUpload
                          {...uploadConfig}
                          // onChange={FileUploadProvider(index, 'template')}
                        ></MyUpload>
                      </Form.Item>
                      <Form.Item
                        name={[field.name, 'description']}
                        // fieldKey={[field.fieldKey, 'description']}
                        label='简介'
                      >
                        <Input.TextArea
                          autoSize={{ minRows: 2 }}
                        ></Input.TextArea>
                      </Form.Item>
                      <Form.Item
                        name={[field.name, 'readme']}
                        // fieldKey={[field.fieldKey, 'readme']}
                        label='说明文档(md)'
                      >
                        <MyUpload
                          {...uploadConfig}
                          accept='.md'
                          // onChange={FileUploadProvider(index, 'template')}
                        ></MyUpload>
                      </Form.Item>
                      {fields.length > 1 ? (
                        <MinusCircleOutlined
                          className='dynamic-delete-button'
                          style={{ margin: '0 8px' }}
                          onClick={() => {
                            remove(field.name);
                          }}
                        />
                      ) : (
                        ''
                      )}
                    </div>
                  ))}
                  <Form.Item label='模版'>
                    <Button
                      type='dashed'
                      onClick={() => {
                        add();
                      }}
                      style={{ width: '60%', marginTop: 10 }}
                    >
                      <PlusOutlined /> 添加模版
                    </Button>
                  </Form.Item>
                </div>
              );
            }}
          </Form.List>
          <Form.Item style={{ textAlign: 'center' }}>
            <Button type='primary' htmlType='submit'>
              保存
            </Button>
          </Form.Item>
        </Form>
      </div>
    </div>
  );
};

export default TemplateUpload;
