import React from 'react';
import { Layout, Menu } from 'antd';
import { Link, BrowserRouter as Router } from 'react-router-dom';
import Routes from './router';

function App() {
  return (
    <Router>
      <Layout style={{ height: '100vh' }}>
        <Layout.Header className="header">
          <h1 style={{ color: '#fff', marginRight: 60 }}>代码生成器</h1>

          <Menu mode="horizontal" theme="dark">
            <Menu.Item>
              <Link to="/">模版列表</Link>
            </Menu.Item>
            <Menu.Item>
              <Link to="/ezcode">本地操作台</Link>
            </Menu.Item>
            <Menu.Item>
              <Link to="/settings">设置</Link>
            </Menu.Item>
            <Menu.Item>
              <Link to="/upload">上传模版</Link>
            </Menu.Item>
          </Menu>
        </Layout.Header>
        <Layout.Content style={{ padding: 20, textAlign: 'left' }}>
          <Routes></Routes>
        </Layout.Content>
      </Layout>
    </Router>
  );
}

export default App;
