import React, { useState, useCallback, useMemo, useEffect } from 'react';
import { Form, Input, Modal, Button, message, Card, Drawer } from 'antd';
import { CopyToClipboard } from 'react-copy-to-clipboard';
import Axios from 'axios';
import { useHistory } from 'react-router-dom';
import { getRuleReg } from '../utils/index';
import RuleForm from '../compoents/RuleForm';
// import Prism from 'prismjs';
// import useRequest from '../hooks/useRequest';

function outputFormValue(array: string[]) {
  const has: Record<string, string | undefined> = {};
  array.forEach((item) => {
    if (has[item] === undefined) {
      has[item] = '';
    }
  });
  return has;
}

function showCode(
  form: Record<string, string>,
  code: string,
  currentFormItem = '',
  rule = {
    prefix: '{{',
    suffix: '}}',
  }
) {
  if (code && form) {
    let rs = code;
    Object.keys(form).forEach((item) => {
      rs = rs.replace(
        new RegExp(`${rule.prefix}${item}${rule.suffix}`, 'gm'),
        `<span class="slot-${item} ${
          item === currentFormItem ? 'actived-code' : ''
        }">${form[item] || ' '}</span>`
      );
    });
    return rs;
  }
  return '';
}

function outputCode(
  form: Record<string, string> = {},
  code: string = '',
  rule = {
    prefix: '{{',
    suffix: '}}',
  }
) {
  let rs = code;
  Object.keys(form).forEach((item) => {
    rs = rs.replace(
      new RegExp(`${rule.prefix}${item}${rule.suffix}`, 'gm'),
      form[item]
    );
  });
  return rs;
}

const Creator: React.FC = () => {
  const history = useHistory();
  const [code, setCode] = useState('');

  //#region 获取规则
  const [rule, setRule] = useState({
    prefix: '',
    content: '',
    suffix: '',
  });
  useEffect(() => {
    Axios.get('/api/getSetting').then((res) => {
      if (res.data) {
        setRule(res.data);
      } else {
        message.error('未设置规则，即将跳转设置！');
        setTimeout(() => {
          history.push('/settings');
        }, 3000);
      }
    });
  }, []);

  useEffect(() => {
    createForm(code, getRuleReg(rule));
  }, [rule, code]);

  /** 分析代码，生成表单 */
  const createForm = (code: string, rule: string) => {
    if (code && rule) {
      const res = code.match(new RegExp(rule, 'gm'));
      if (res) {
        setFormValues(outputFormValue(res));
      } else {
        message.error('代码中不存在插槽！');
      }
    }
  };

  /** 设置代码，高亮，更新表单 */
  const setCodeCtn = (code: string) => {
    setCode(code);
    createForm(code, getRuleReg(rule));
    setTimeout(Prism.highlightAll, 0);
  };

  //#region 表单相关
  const [formValues, setFormValues] = useState({});

  const handleValueChange = useCallback(
    (changeValue: any) => {
      setFormValues({ ...formValues, ...changeValue });
    },
    [formValues]
  );

  /** 设置插槽高亮 */
  const [currentFormItem, setCurrentFormItem] = useState('');

  const renderForm = useCallback(
    (formValues: Record<string, string>) => (
      <Form
        labelCol={{ span: 8 }}
        onValuesChange={(changeValue) => handleValueChange(changeValue)}
      >
        {Object.keys(formValues).map((item) => (
          <Form.Item name={item} key={item} label={item} required>
            <Input
              placeholder="请输入"
              onFocus={() => setCurrentFormItem(item)}
            ></Input>
          </Form.Item>
        ))}
      </Form>
    ),
    [handleValueChange]
  );
  const form = useMemo(() => renderForm(formValues), [formValues, renderForm]);
  //#endregion

  //#region  导入代码相关
  const [visible, setVisible] = useState(false);
  const [tempCode, setTempCode] = useState('');
  const showCodeModal = () => {
    setVisible(true);
    setTempCode(code);
  };
  const modalConfirm = () => {
    setCodeCtn(tempCode);
    setVisible(false);
  };
  const modalCancel = () => {
    setTempCode('');
    setVisible(false);
  };
  //#endregion

  const setHightLight = () => {
    setCurrentFormItem('');
    setTimeout(Prism.highlightAll, 0);
  };

  //#region 本地规则抽屉
  const [dvisible, setDVisible] = useState(false);

  //#endregion

  return (
    <div style={{ display: 'flex', height: '100%', padding: 20 }}>
      <Card
        style={{ flex: 1 }}
        title="代码预览区域"
        extra={
          <div>
            {currentFormItem !== '' && (
              <Button onClick={setHightLight}>代码高亮</Button>
            )}
            <Button type="primary" onClick={showCodeModal}>
              导入/编辑代码
            </Button>
            <CopyToClipboard
              text={outputCode(formValues, code, rule)}
              onCopy={() => message.success('复制成功')}
            >
              <Button type="primary">复制代码</Button>
            </CopyToClipboard>
            <Button type="primary" onClick={() => setDVisible(true)}>
              设置临时规则
            </Button>
          </div>
        }
      >
        <pre className=" language-tsx">
          {React.createElement('code', {
            className: 'language-tsx',
            dangerouslySetInnerHTML: {
              __html: code
                ? showCode(formValues, code, currentFormItem, rule)
                : '请导入代码',
            },
          })}
        </pre>
      </Card>
      <Card title="模版代码配置" style={{ width: '500px', marginLeft: 20 }}>
        <div>{form}</div>
      </Card>
      <Modal
        visible={visible}
        title="导入代码【可编辑】"
        onOk={modalConfirm}
        onCancel={modalCancel}
      >
        <Input.TextArea
          onChange={({ target }) => setTempCode(target.value)}
          value={tempCode}
          allowClear
          autoSize={{ minRows: 10 }}
        ></Input.TextArea>
      </Modal>
      <Drawer
        title="临时规则设置"
        placement="right"
        width={400}
        closable={false}
        onClose={() => setDVisible(false)}
        visible={dvisible}
      >
        <RuleForm
          {...rule}
          onConfirm={(value: any) => {
            setDVisible(false);
            setRule(value);
          }}
        ></RuleForm>
      </Drawer>
    </div>
  );
};

export default Creator;
