import React, { useEffect, useState, useCallback, useMemo } from 'react';
import {
  Form,
  Input,
  Modal,
  Button,
  message,
  Card,
  Tabs,
  Descriptions,
  Select,
  Drawer,
} from 'antd';
import { CopyToClipboard } from 'react-copy-to-clipboard';
import { useParams, useHistory } from 'react-router-dom';
import Axios from 'axios';
import { saveAs } from 'file-saver';
import marked from 'marked';
import DOMPurify from 'dompurify';
import { getRuleReg } from '../utils/index';
import { Package } from './types';
import RuleForm from '../compoents/RuleForm';

const { TabPane } = Tabs;

interface FileItem {
  name: string;
  code: string;
}

interface ReadmeItem {
  name: string;
  content: string;
}
type ReadmeNullItem = ReadmeItem | null;

function outputFormValue(array: string[]) {
  const has: Record<string, string> = {};
  array.forEach((item) => {
    if (has[item] === undefined) {
      has[item] = '';
    }
  });
  return has;
}

function showCode(
  form: Record<string, string>,
  code: string,
  currentFormItem = '',
  rule = {
    prefix: '{{',
    suffix: '}}',
  }
) {
  if (code && form) {
    let rs = code;
    Object.keys(form).forEach((item) => {
      rs = rs.replace(
        new RegExp(`${rule.prefix}${item}${rule.suffix}`, 'gm'),
        `<span class="slot-${item} ${
          item === currentFormItem ? 'actived-code' : ''
        }">${form[item] || ' '}</span>`
      );
    });
    return rs;
  }
  return '';
}

/** 导出模版 */
function exportTemplate(
  file: FileItem,
  formValue: Record<string, string>,
  rule = {
    prefix: '{{',
    suffix: '}}',
  }
) {
  const blob = new Blob([outputCode(formValue, file.code, rule)], {
    type: 'text/plain;charset=utf-8',
  });
  saveAs(blob, file.name);
}
function exportPatch(
  files: FileItem[],
  formValues: Record<string, string>[],
  rule = {
    prefix: '{{',
    suffix: '}}',
  }
) {
  files.forEach((item, index) => {
    exportTemplate(item, formValues[index], rule);
  });
}

function outputCode(
  form: Record<string, string> = {},
  code: string = '',
  rule = {
    prefix: '{{',
    suffix: '}}',
  }
) {
  let rs = code;
  Object.keys(form).forEach((item) => {
    rs = rs.replace(
      new RegExp(`${rule.prefix}${item}${rule.suffix}`, 'gm'),
      form[item]
    );
  });
  return rs;
}

const Creator: React.FC = () => {
  const history = useHistory();
  const { name, version } = useParams();
  const [detail, setDetail] = useState<Package>({
    name: '',
    author: '',
    version: '1.0.0',
    description: '',
    files: [],
  });
  useEffect(() => {
    Axios.get(`/api/getPackage?name=${name}&version=${version}`).then((res) => {
      setDetail(res.data.data);
    });
  }, [name, version]);

  const [code, setCode] = useState('');

  const [files, setFiles] = useState<FileItem[]>([]);
  const [readmes, setReadmes] = useState<ReadmeNullItem[]>([]);

  const [rule, setRule] = useState({
    prefix: '',
    content: '',
    suffix: '',
  });
  useEffect(() => {
    Axios.get('/api/getSetting').then((res) => {
      if (res.data) {
        setRule(res.data);
      } else {
        message.error('未设置规则，即将跳转设置！');
        setTimeout(() => {
          history.push('/settings');
        }, 3000);
      }
    });
  }, []);

  useEffect(() => {
    createForm(code, getRuleReg(rule));
  }, [rule, code]);

  // 加载云端文件
  useEffect(() => {
    if (detail.files.length > 0) {
      Axios.all(detail.files.map((item) => Axios.get(item.template.url))).then(
        Axios.spread(function (...props) {
          // Both requests are now complete
          setFiles(
            props.map((item, index) => ({
              name: detail.files[index].template.name,
              code: item.data,
            }))
          );
          setCodeCtn(props[0].data);
        }),
        () => {
          message.error('模版文件加载失败');
        }
      );
      // 加载readme
      Axios.all(
        detail.files.map((item) =>
          item.readme ? Axios.get(item.readme.url) : null
        )
      ).then(
        Axios.spread(function (...props) {
          console.log(props);
          // Both requests are now complete
          setReadmes(
            props.map((item, index) =>
              item
                ? {
                    name: detail.files[index].readme.name,
                    content: item.data,
                  }
                : null
            )
          );
          // setCodeCtn(props[0].data);
        }),
        () => {
          message.error('readme加载失败');
        }
      );
    }
  }, [detail.files]);

  //#region  tab控制
  const [index, setIndex] = useState(0);

  const handleTabChange = (key: string) => {
    setIndex(Number(key));
  };

  // 设置code
  useEffect(() => {
    if (files && files.length > 0) {
      setCodeCtn(files[Number(index)].code);
    }
  }, [index, files]);
  //#endregion

  /** 设置代码，高亮，更新表单 */
  const setCodeCtn = (code: string) => {
    setCode(code);
    createForm(code, getRuleReg(rule));
    setTimeout(Prism.highlightAll, 0);
  };

  //#region 表单相关
  const [form] = Form.useForm();
  const [formValues, setFormValues] = useState<Record<string, string>[]>([]);

  /** 分析代码，生成表单 */
  const createForm = (code: string, rule: string) => {
    if (code && rule) {
      if (formValues[index]) {
        form.setFieldsValue(formValues[index]);
      } else {
        const res = code.match(new RegExp(rule, 'gm'));
        const newFormValues = [...formValues];
        if (res) {
          newFormValues[index] = outputFormValue(res);
          form.setFieldsValue(newFormValues[index]);
        } else {
          newFormValues[index] = {};
          form.resetFields();
          message.error('代码中不存在插槽！');
        }
        setFormValues(newFormValues);
      }
    }
  };

  const handleValueChange = useCallback(
    (changeValue: any) => {
      const newFormValues = [...formValues];
      newFormValues[index] = { ...newFormValues[index], ...changeValue };
      setFormValues(newFormValues);
      // 设置表单数据
      form.setFieldsValue(newFormValues[index]);
    },
    [formValues, index]
  );

  /** 设置插槽高亮 */
  const [currentFormItem, setCurrentFormItem] = useState('');

  const renderForm = useCallback(
    (formValues: Record<string, string> = {}) =>
      Object.keys(formValues).map((item) => (
        <Form.Item name={item} key={item} label={item} required>
          <Input
            placeholder="请输入"
            onFocus={() => setCurrentFormItem(item)}
          ></Input>
        </Form.Item>
      )),
    [handleValueChange]
  );
  const formElement = useMemo(
    () => (formValues.length > 0 ? renderForm(formValues[index]) : ''),
    [formValues, renderForm, index]
  );

  /** 继承插槽值 */
  const handleExtendsFormValues = (value: any) => {
    const formValue = formValues[Number(value)];
    const newFormValues = [...formValues];
    newFormValues[index] = { ...formValues[index], ...formValue };
    setFormValues(newFormValues);
    form.setFieldsValue(newFormValues[index]);
  };
  //#endregion

  //#region  导入代码相关
  const [visible, setVisible] = useState(false);
  const [tempCode, setTempCode] = useState('');
  const showCodeModal = () => {
    setVisible(true);
    setTempCode(code);
  };
  const modalConfirm = () => {
    setCodeCtn(tempCode);
    setVisible(false);
  };
  const modalCancel = () => {
    setTempCode('');
    setVisible(false);
  };
  //#endregion

  /** 代码高亮 */
  const setHightLight = () => {
    setCurrentFormItem('');
    setTimeout(Prism.highlightAll, 0);
  };

  //#region 本地规则抽屉
  const [dvisible, setDVisible] = useState(false);

  //#endregion

  return (
    <div style={{ display: 'flex', height: '100%', padding: 20, flex: 1 }}>
      <Card
        style={{
          flex: 1,
          display: 'flex',
          flexDirection: 'column',
          overflow: 'auto',
        }}
        headStyle={{ flexShrink: 0 }}
        title="代码预览区域"
        extra={
          <div>
            {currentFormItem !== '' && (
              <Button onClick={setHightLight}>代码高亮</Button>
            )}
            <Button type="primary" onClick={showCodeModal} disabled>
              导入/编辑代码
            </Button>
            <CopyToClipboard
              text={
                formValues.length > 0 &&
                outputCode(formValues[index], code, rule)
              }
              onCopy={() => message.success('复制成功')}
            >
              <Button type="primary">复制代码</Button>
            </CopyToClipboard>
            <Button
              type="primary"
              onClick={() => exportPatch(files, formValues, rule)}
            >
              导出模版
            </Button>
            <Button type="primary" onClick={() => setDVisible(true)}>
              设置临时规则
            </Button>
          </div>
        }
      >
        <Descriptions title="基本信息">
          <Descriptions.Item label="作者">{detail.name}</Descriptions.Item>
          <Descriptions.Item label="版本">{detail.version}</Descriptions.Item>
          <Descriptions.Item label="简介">
            {detail.description}
          </Descriptions.Item>
        </Descriptions>
        {files && files.length > 0 && (
          <Tabs defaultActiveKey={String(index)} onChange={handleTabChange}>
            {files.map((item, index) => (
              <TabPane tab={item.name} key={String(index)}>
                <pre className={` language-${item.name.split('.')[1]}`}>
                  <code
                    className={`language-${item.name.split('.')[1]}`}
                    dangerouslySetInnerHTML={{
                      __html: DOMPurify.sanitize(
                        formValues.length > 0
                          ? showCode(
                              formValues[index],
                              code,
                              currentFormItem,
                              rule
                            )
                          : ''
                      ),
                    }}
                  ></code>
                </pre>
                {readmes[index] && (
                  <Card title={<div style={{ color: '#1890ff' }}>Readme</div>}>
                    <div
                      className="markdown"
                      dangerouslySetInnerHTML={{
                        __html: DOMPurify.sanitize(
                          marked(readmes[index]?.content || '')
                        ),
                      }}
                    ></div>
                  </Card>
                )}
              </TabPane>
            ))}
          </Tabs>
        )}
      </Card>
      <Card
        title={`【${files[index]?.name}】配置`}
        style={{ width: '500px', marginLeft: 20 }}
        extra={
          <Button
            type="primary"
            onClick={() =>
              exportTemplate(files[index], formValues[index], rule)
            }
          >
            导出当前文件
          </Button>
        }
      >
        <div>
          <Form
            form={form}
            labelCol={{ span: 8 }}
            onValuesChange={(changeValue) => handleValueChange(changeValue)}
          >
            {index > 0 && (
              <Form.Item label="插槽值继承">
                <Select
                  placeholder="选择模版插槽继承"
                  onSelect={(value) => handleExtendsFormValues(value)}
                  allowClear
                >
                  {files.map((item, i) => (
                    <Select.Option key={i} value={i} disabled={i === index}>
                      {item.name}
                    </Select.Option>
                  ))}
                </Select>
              </Form.Item>
            )}
            {formElement}
          </Form>
        </div>
      </Card>
      <Drawer
        title="临时规则设置"
        placement="right"
        width={400}
        closable={false}
        onClose={() => setDVisible(false)}
        visible={dvisible}
      >
        <RuleForm
          {...rule}
          onConfirm={(value: any) => {
            setDVisible(false);
            setRule(value);
          }}
        ></RuleForm>
      </Drawer>
      <Modal
        visible={visible}
        title="导入代码【可编辑】"
        onOk={modalConfirm}
        onCancel={modalCancel}
      >
        <Input.TextArea
          onChange={({ target }) => setTempCode(target.value)}
          value={tempCode}
          allowClear
          autoSize={{ minRows: 10 }}
        ></Input.TextArea>
      </Modal>
    </div>
  );
};

export default Creator;
